/*
* anim
*/
.anim-fast {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.all-transitions {
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}
.main_shadow {
  -webkit-box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
}
.popover {
  background-color: #ffffff;
  color: #575757;
  font-family: 'Open Sans';
  font-size: 1em;
  position: absolute;
  width: 300px;
  min-height: 100px;
  border-radius: 3px;
  z-index: 5;
  border: 0px solid black;
  padding: 5px;
  filter: drop-shadow(0 0 1px rgba(0, 0, 0, 0.4)) drop-shadow(0 3px 4px rgba(0, 0, 0, 0.4));
  -webkit-filter: drop-shadow(0 0 1px rgba(0, 0, 0, 0.4)) drop-shadow(0 3px 4px rgba(0, 0, 0, 0.4));
  text-align: left;
  /* arrow */
}
.popover img {
  width: 100%;
}
.popover ul {
  padding: 5px;
}
.popover ul .key {
  font-weight: 800;
}
.popover ul .value {
  font-weight: 500;
}
.popover ul li {
  padding-bottom: 0.4em;
}
.popover:before {
  content: ' ';
  width: 15px;
  height: 15px;
  right: 17px;
  top: -5px;
  position: absolute;
  bottom: auto;
  border-right-color: #000;
  background-color: white;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  border-left: 0px solid black;
  border-bottom: 0px solid black;
  border-radius: 2px;
  z-index: -1;
}
.popover.left:before {
  left: -10px;
  bottom: 40px;
  /*
      box-shadow: -5px 3px 5px 0px rgba(0, 0, 0, 0.22);
      */
}
.popover.right {
  min-height: 200px;
}
.popover.right:before {
  right: 10px;
  top: 15px;
  /*
      box-shadow: 5px -3px 5px 0px rgba(0, 0, 0, 0.22);
      */
}

