/*
* anim
*/
.anim-fast {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.all-transitions {
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}
.main_shadow {
  -webkit-box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
}
#myeditor {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100%;
}
#myeditor #project-actions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#myeditor .shortcut {
  background: #FF356B;
}
#editor_container {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  -webkit-transition: -webkit-transform 300ms ease-in-out;
  transition: -webkit-transform 300ms ease-in-out;
  transition: transform 300ms ease-in-out;
  transition: transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  border-bottom: 0px;
  border-radius: 2px 2px 0 0;
  overflow: hidden;
}
#editor_container.slide {
  -webkit-transform: translateY(420px);
          transform: translateY(420px);
}
#editor_container #editor {
  position: relative;
  height: calc(100%);
}
#editor_container #nav_tabs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  border-bottom: 0px;
  padding-left: 0;
  margin-bottom: 0;
  /* border-bottom: 2px solid @accentColor; */
}
#editor_container #nav_tabs #project_run {
  padding: 0 20px;
}
#editor_container #nav_tabs #project_save {
  padding: 0 10px;
}
#editor_container #nav_tabs button {
  background: transparent;
  color: white;
  font-size: 1em;
}
#editor_container #nav_tabs button:active {
  background: #22221d !important;
}
#editor_container #nav_tabs button:hover {
  background: #2c2e27;
  color: #FF356B;
}
#editor_container #nav_tabs button.torun {
  color: #FF356B;
}
#editor_container #nav_tabs #tabs {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
  list-style: none;
  color: white;
  font-weight: 600;
  font-size: 0.8em;
}
#editor_container #nav_tabs #tabs li {
  position: relative;
  display: inline-block;
  padding: 18px 20px;
  cursor: pointer;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  border-bottom: 3px solid rgba(0, 0, 0, 0);
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 200px;
  white-space: nowrap;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#editor_container #nav_tabs #tabs li.active {
  border-bottom: 2px solid white;
}
#editor_container #nav_tabs #tabs li:hover {
  background-color: #2c2e27;
}
#editor_container #nav_tabs #tabs li:hover .close {
  display: block;
}
#editor_container #nav_tabs #tabs li a {
  margin-left: 15px;
  font-size: 0.8em;
  margin-bottom: 0px;
  border-radius: 2px 2px 0px 0px;
  border: 0px;
}
#editor_container #nav_tabs #tabs li.isModified {
  border-color: #FF356B;
  font-style: italic;
}
#editor_container #nav_tabs #tabs li .close {
  display: none;
  position: absolute;
  top: 5px;
  right: 2px;
  color: gray;
  font-size: 0.8em;
}
#editor_container #nav_tabs #tabs li .close:hover {
  color: black;
}
#editor_container #nav_tabs #add_tab {
  cursor: pointer;
  color: rgba(0, 0, 0, 0.5);
  padding: 1.3em;
}
#editor_container #nav_tabs #add_tab:hover {
  background: rgba(0, 0, 0, 0.1);
}
#editor_container #nav_tabs #add_tab:active {
  background: rgba(0, 0, 0, 0.5);
}
#editor_container #project_name {
  cursor: pointer;
  font-size: 0.7em;
  padding: 0px 10px;
  margin-right: 5px;
  color: rgba(255, 255, 255, 0.8);
  min-width: 50px;
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#editor_container #project_name .folder {
  padding-bottom: 4px;
  font-weight: 500;
}
#editor_container #project_name .name::before {
  content: "\2514";
  padding-right: 2px;
}
#editor_container #project_name .name {
  margin-left: 5px;
  font-weight: 700;
}
#editor_container .msg_error {
  position: absolute;
  z-index: 5;
}
#project-options {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 18px 0px 18px 0px;
  /* padding: 26px 0px 26px 0px; */
}
