/*
* anim
*/
.anim-fast {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.all-transitions {
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}
.main_shadow {
  -webkit-box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
}
#console {
  background: rgba(0, 0, 0, 0.9) !important;
  height: 100% !important;
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}
#console .actionbar ul li:hover,
#console .actionbar ul li.enabled {
  color: white !important;
}
#console .content {
  color: white !important;
}
#console .content ul {
  height: calc(100%);
  width: 100%;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#console .content ul li {
  padding: 2px 0px;
  border-bottom: 1px dashed #333;
  font-family: Source Code Pro;
  line-height: 1.8em;
  word-break: break-all;
}
#console .content ul li img {
  width: 100%;
  max-width: 320px;
}
#console .content ul li.log_error {
  border-left: 2px solid rgba(255, 0, 54, 0.88);
  padding-left: 3px;
}
#console .content ul li.log_error::before {
  content: "error";
  background: rgba(255, 0, 54, 0.88);
  padding: 1px 2px;
}
#console .content ul li.log_error:hover {
  background: rgba(255, 51, 94, 0.88);
}
#console .content ul li:hover {
  background-color: rgba(255, 255, 255, 0.05);
}
#console .content ul li .time {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.8em;
}
#console .content ul li .time.off {
  display: none;
}

