/*
* anim
*/
.anim-fast {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.all-transitions {
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}
.main_shadow {
  -webkit-box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
}
.vertical #card {
  border-top: solid 0px #54524f;
}
.horizontal #card {
  border-left: solid 0px #54524f;
}
#info {
  font-size: 0.8em;
  position: relative;
}
#info > * {
  padding: 5px 0px;
  color: black;
}
#info h2 {
  font-family: 'Source Code Pro';
  font-size: 1em;
  font-weight: 600;
}
#info h2 .method {
  border-bottom: 2px solid #FF356B;
}
#info h3 {
  padding: 5px 0px;
  color: #555;
  font-weight: 300;
  font-size: 0.9em;
  text-transform: uppercase;
}
#info table {
  width: 100%;
}
#info code {
  padding: 5px 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  background: whitesmoke;
  border-radius: 2px;
  border: 1px solid rgba(0, 0, 0, 0.08);
}
#info code,
#info .usage > ul,
#info .param,
#info .returns .type,
#info .related {
  font-family: 'Source Code Pro';
  overflow: initial;
  padding: 4px 0px;
}
#info .description {
  font-weight: 300;
  line-height: 1.3em;
}
#info .usage li {
  padding: 2px;
}
#info .stub {
  margin: 10px 0px;
  display: block;
  color: white;
  font-size: 0.8em;
  /* animation: fadeIn 1s infinite alternate; */
}
#info .stub a {
  padding: 5px 5px;
  background: #ec4f4f;
  border-radius: 2px;
  color: white;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
  margin-right: 2px;
  margin-bottom: 2px;
  border: 1px solid rgba(255, 255, 255, 0.11);
}
#info .stub a:hover {
  opacity: 0.8;
}
#info .stub a:active {
  opacity: 0.6;
}
#info .example a {
  color: #FF356B;
  margin: 10px 0px;
  display: block;
}
#info .btn-close {
  color: black;
  right: 0px;
  top: -8px;
}
@-webkit-keyframes fadeIn {
from {
    opacity: 0.8;
}
}
@keyframes fadeIn {
from {
    opacity: 0.8;
}
}
