/*
* anim
*/
.anim-fast {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.all-transitions {
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}
.main_shadow {
  -webkit-box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
}
.tutorial_chooser {
  z-index: 2;
}
.tutorial_chooser a {
  padding: 10px;
  cursor: pointer;
}
.tutorial_chooser a:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.tutorial_chooser a.selected {
  background-color: rgba(0, 0, 0, 0.2);
}
#tutorial {
  overflow-y: hidden;
  color: #222;
  background-color: white;
  -webkit-box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
  line-height: 1.1em;
  font-size: 1em;
}
#tutorial code {
  font-family: Source Code Pro;
}
#tutorial h1,
#tutorial h2,
#tutorial h3,
#tutorial h4,
#tutorial h5,
#tutorial h6,
#tutorial p,
#tutorial ul,
#tutorial ol,
#tutorial li,
#tutorial blockquote,
#tutorial table,
#tutorial pre {
  padding: 0em 0em;
  margin-bottom: 0.5em;
}
#tutorial h1,
#tutorial h2,
#tutorial h3,
#tutorial h4,
#tutorial h5,
#tutorial h6 {
  margin: 1em 0em 0.5em 0em;
  font-weight: bold;
}
#tutorial h1,
#tutorial h2 {
  border-bottom: 1px solid #ccc;
  padding-bottom: 0.3em;
}
#tutorial h1 {
  font-size: 2.2em;
}
#tutorial h2 {
  font-size: 1.8em;
}
#tutorial h3 {
  font-size: 1.6em;
}
#tutorial h4 {
  font-size: 1.5em;
}
#tutorial h5 {
  font-size: 1.4em;
}
#tutorial h6 {
  font-size: 1.3em;
}
#tutorial strong {
  font-weight: bold;
}
#tutorial em {
  font-style: italic;
}
#tutorial ul,
#tutorial ol {
  margin: 5px 15px;
}
#tutorial ul {
  list-style: circle;
}
#tutorial ol {
  list-style: decimal;
}
#tutorial pre {
  overflow: scroll;
  background-color: #272822;
  border-radius: 2px;
  padding: 8px;
  color: #ddd;
  font-size: 1.2em;
  line-height: 1.3em;
}
#tutorial blockquote {
  border-left: 3px solid #eee;
  margin: 0px;
  padding-left: 8px;
}
#tutorial table {
  margin: 10px 0px;
}
#tutorial table tr {
  border-top: 1px solid #ccc;
}
#tutorial table tr th,
#tutorial table tr td {
  padding: 5px 12px;
  border: 1px solid #ddd;
}
#tutorial table tr th {
  font-weight: bold;
}
#tutorial .video_wrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0;
}
#tutorial .video_wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

