.gpalette {
  color: black;
  background: #f1f1f1;
  border-bottom: 2px solid rgba(0, 0, 0, 0.2);
  height: 100%;
  position: absolute;
  right: 0;
  -webkit-filter: drop-shadow(0 0 1px rgba(0, 0, 0, 0.4)) drop-shadow(0 3px 4px rgba(0, 0, 0, 0.4));
          filter: drop-shadow(0 0 1px rgba(0, 0, 0, 0.4)) drop-shadow(0 3px 4px rgba(0, 0, 0, 0.4));
}
.gpalette .header {
  cursor: pointer;
  -webkit-transform: translate3d(-12px, 72px, 0px) rotate3d(0, 0, 1, -90deg);
          transform: translate3d(-12px, 72px, 0px) rotate3d(0, 0, 1, -90deg);
  -webkit-transform-origin: left;
          transform-origin: left;
  padding: 6px 18px;
  position: absolute;
  border-radius: 2px 2px 0px 0px;
  font-weight: 600;
  color: gray;
  border-bottom: 0px solid rgba(0, 0, 0, 0.05);
  background: #f1f1f1;
}
.gpalette .header:hover {
  background: #dedede;
}
.gpalette .header button {
  color: white;
}
.gpalette #search {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  width: 100%;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.gpalette #search input {
  width: 100%;
  background: rgba(0, 0, 0, 0.57);
  outline: none;
  margin: 8px;
  border: 2px solid rgba(0, 0, 0, 0.06);
  border-radius: 12px;
  padding: 2px 12px;
  color: white;
}
.gpalette .module_list {
  font-family: monospace;
  padding: 10px;
  border-bottom: 3px solid #e8e8e8;
  margin-bottom: 5px;
}
.gpalette .module_list h2 {
  padding: 0px 0px 2px 0px;
  color: gray;
  font-size: 0.7em;
}
.gpalette .module_list li {
  cursor: pointer;
  display: inline-table;
  text-align: center;
  font-size: 0.7em;
  padding: 2px;
}
.gpalette .module_list li:hover {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 2px;
}
.gpalette .module_list img {
  width: 50px;
  display: inline-block;
  float: left;
